	width: 100%;

	height: 100%;

}
/*stage start*/
.stage{
    position: relative;

    width: 100%;
    height: 720px;
   

}

/*stage end*/

/*imaghe start*/

  .img-sec{

  	position: relative;

  	width: 100%;
  	height: 100%;

  	overflow: hidden;

  	background-color: #ddd;

  	-webkit-perspective: 1000px;

  	        perspective: 1000px;


  }

/*image  end*/
/*controller star */



/*controler end*/


.img-sec .img-figure{

	position: absolute;

	width: 320px;
	height: 360px;
	margin :0;
    padding: 40px;
    background-color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transform-origin: 0 50% 0;
        -ms-transform-origin: 0 50% 0;
            transform-origin: 0 50% 0;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    cursor: pointer;

    -webkit-transition : left 0.8s ease-out, top 0.8s ease-out, -webkit-transform 0.8s ease-out;

    transition : left 0.8s ease-out, top 0.8s ease-out, -webkit-transform 0.8s ease-out;

    -o-transition : transform 0.8s ease-out, left 0.8s ease-out, top 0.8s ease-out;

    transition : transform 0.8s ease-out, left 0.8s ease-out, top 0.8s ease-out;

    transition : transform 0.8s ease-out, left 0.8s ease-out, top 0.8s ease-out, -webkit-transform 0.8s ease-out;
    
    
}

.img-sec .Iverse{

	position: absolute;

	width: 320px;
	height: 360px;
	margin :0;
    padding: 40px;
    background-color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;

    -webkit-transform-style: preserve-3d;

            transform-style: preserve-3d;
    -webkit-transform: translate(320px) rotateY(180deg);
            transform: translate(320px) rotateY(180deg);

    -webkit-transition : -webkit-transform 0.8s ease-out;

    transition : -webkit-transform 0.8s ease-out;

    -o-transition : transform 0.8s ease-out;

    transition : transform 0.8s ease-out;

    transition : transform 0.8s ease-out, -webkit-transform 0.8s ease-out;

    
}


.img-sec .img-figure .is-back{

	position: relative;
	left:-40px;
	top: -360px;

	width: 320px;
	height:360px;
	line-height: 360px;
	margin :0;
    padding: 40px;
    background-color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
  

    -webkit-transform-style: preserve-3d;
  

            transform-style: preserve-3d;
    -webkit-transform: rotateY(180deg) translateZ(1px);
            transform: rotateY(180deg) translateZ(1px);

    cursor: pointer;


}




figureCaption {
    text-align: center;

}

figureCaption .img-title{
	margin: 20px 0 0;
