/* 导航的部分*/

.controller-nav{

	position: absolute;
	left: 0;
	bottom: 30px;
	z-index:101;
    width:100%;
    height: 55px;
    text-align: center;

}

.controller{

	display: inline-block;
	width: 35px;
	height: 35px;
	border-radius: 35px;
	margin: 0 15px;
	background-color: #848181;

    vertical-align: middle;

    -webkit-transition: background-color 0.5s ease-in-out, width .8s ease-in-out,height .8s ease-in-out, border-radius .8s ease-in-out, font-size .8s ease-in-out, -webkit-transform .8s ease-in-out;

    transition: background-color 0.5s ease-in-out, width .8s ease-in-out,height .8s ease-in-out, border-radius .8s ease-in-out, font-size .8s ease-in-out, -webkit-transform .8s ease-in-out;

    -o-transition: background-color 0.5s ease-in-out, transform .8s ease-in-out, width .8s ease-in-out,height .8s ease-in-out, border-radius .8s ease-in-out, font-size .8s ease-in-out ;

    transition: background-color 0.5s ease-in-out, transform .8s ease-in-out, width .8s ease-in-out,height .8s ease-in-out, border-radius .8s ease-in-out, font-size .8s ease-in-out ;

    transition: background-color 0.5s ease-in-out, transform .8s ease-in-out, width .8s ease-in-out,height .8s ease-in-out, border-radius .8s ease-in-out, font-size .8s ease-in-out, -webkit-transform .8s ease-in-out;

    cursor: pointer;
    -webkit-transform: scale(0.6);
        -ms-transform: scale(0.6);
            transform: scale(0.6);


}

.controller:hover{

	background-color: #cab5b5;
}

.isActive{
	
	-webkit-transform: scale(1);
	
	    -ms-transform: scale(1);
	
	        transform: scale(1);
}


.isActive::before{

	font-family: 'ico-arrow';
	content:'\e965';
    font-size: 20px;
    line-height: 35px;
    color: #fff;

}

.isInverse{
   
	-webkit-transform: rotateY(180deg);
   
	        transform: rotateY(180deg);
}

